/**
 * 
 */
import {global} from './global';

	var getJSONP = (url,data) =>{
		return new Promise((resolve, reject) => {
			getData(url, data, "POST", "jsonp",
			resolve, reject);
		});
	}
	var  getJSON = (url,data) =>{
		return new Promise((resolve, reject) => {
			getData(url, data, "POST", "json",
			resolve, reject);
		});
	}

	const getData = (url, data, method, dataType, success, error ,theOptions) => {
		var options = {
			url: url,
			method: method,
			data: data,
			dataType: dataType
		};
		$.extend(options, theOptions);
		if(typeof data == 'string'){
			options.contentType  = 'application/json';
		}
		var request = $.ajax(options);
		request.done(function( data ) {
			if(dataType=='json' || dataType=='jsonp'){ // dataType == json || jsonp
				if(typeof data == 'object' && typeof data.statusCode != 'undefined'){
					if(data.statusCode==0){
						if(typeof success == 'function'){
							success.call(this, data.data,request);
						}
					}else{
						if(typeof error == 'function'){
							error.call(this, data.statusDesc,request);
						}else{
							alert(data.statusDesc);
						}
					}
				}
			}
			else{ //dataType == html
				if(typeof data == 'string' && data != 'error'){
					if(typeof success == 'function'){
						success.call(this, data,request);
					}
				}else{
					if(typeof error == 'function'){
						error.call(this, '',jqXHR);
					}else{
						alert('');
					}
				}
			}
		});

		request.fail(function( jqXHR, textStatus ) {
			if(jqXHR.status == 401){
				//TODO go to login page
				// window.location.href="#login";
				$('#login').animate({top: '0px'});
				$('#mask').css({height: $(window).height()}).show();
				return;
			}
			if(typeof error == 'function'){
				error.call(this, '',jqXHR);
			}else{
				alert('');
			}
		});
	}


export 	{getJSON, getJSONP };



